import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    idToPage: 'terro'
  },
  getters: {
    getIdToPage: (state) => {
      return state.idToPage
    }
  },
  mutations: {
    setIdToPage (state, id) {
      state.idToPage = id
    }
  }
})
