//const ws = new WebSocket("ws://127.0.0.1:8083");
const ws = new WebSocket("wss://techwave.pagekite.me:8083");
export class WebSocketMAnager {
  static socket
  static connectWebSocket(webSocketURL) {
    this.socket = null
    this.socket = new WebSocket(webSocketURL)
    this.socket.onopen = function () {
      console.info('Connection established')
    }
  }
  // fonction pour envoyer un message sur le socket
  static sendWebSocketMessage(message) {
    if (ws.readyState === WebSocket.OPEN) {
      ws.send(JSON.stringify(message));
    }
  }

  // fonction pour recevoir des messages depuis le socket
  static receiveWebSocketMessage(callback) {
    ws.onmessage = function (event) {
      //const data = JSON.parse(event.data);
      callback(event.data);
    };
  }

  // fonction pour fermer la connexion du socket
  static closeWebSocketConnection() {
    ws.close();
  }
}
